/* custom-navbar.css */
/* Style the entire navbar container */
.navbar {
    background: rgb(63,94,251);
background: radial-gradient(circle, rgba(63,94,251,1) 0%, rgba(252,136,70,0.4800770650056898) 54%);
}

/* Style the brand/logo */
.navbar-brand {
    font-size: 24px;
}

/* Style navigation links */
.navbar-nav .nav-link {
    color: black;
    font-family:Dancing Script;
    font-size: 10px;
    margin-left: 20px;
}

/* Change the background color on hover for navigation links */
.navbar-nav .nav-link:hover {
    background-color: mediumorchid;
}

/* Style the toggle button on mobile */
.navbar-toggler-icon {
    background-color:violet
}

/* Style the dropdown menu */
.dropdown-menu {
    background-color: #333;
    color: violet;
}

/* Style individual items within the dropdown menu */
.dropdown-item {
color: cadetblue;
}

/* Style the active navigation item */
.navbar-nav .active .nav-link {
    background-color: #555;
}
.companyimg{
    border-radius: 15px;
    height: 60px;
    width: 90px;
}


.button {
    margin-left: 10px;
    text-decoration: none;
    color: rgba(255, 255, 255, 0.8);
    background: rgb(145, 92, 182);
    padding: 15px 40px;
    border-radius: 4px;
    font-weight: normal;
    text-transform: uppercase;
    transition: all 0.2s ease-in-out;
  }
  
  .glow-button:hover {
    color: rgba(255, 255, 255, 1);
    box-shadow: 0 5px 15px rgba(145, 92, 182, .4);
  }
